module.exports = function (data) {
  var width = 25;
  var height = 6;
  var pixels = data.split("");
  var pixelsPerLayer = width * height;
  var numLayers = data.length / pixelsPerLayer;
  var leastZeros = [151, ""];

  for (var layerNum = 0; layerNum < numLayers; layerNum++) {
    var first = layerNum * 150;
    var last = first + 149;
    var layer = data.slice(first, last + 1);
    var zeros = layer.replace(/[^0]/g, "").length;

    if (zeros < leastZeros[0]) {
      leastZeros[0] = zeros;
      leastZeros[1] = layer;
    }
  }

  var ones = leastZeros[1].replace(/[^1]/g, "").length;
  var twos = leastZeros[1].replace(/[^2]/g, "").length;
  return ones * twos;
};