var relativeBase = 0;

module.exports = function (ints, inputs) {
  var outputs = [];
  var shouldExit = false;
  var index = 0;
  relativeBase = 0;

  while (!shouldExit) {
    var commandInt = ints[index];

    if (commandInt === "99") {
      return outputs;
    }

    var command = getCommand(String(commandInt));

    if (command.command) {
      index = command.command(ints, inputs, outputs, command.paramModes, index);
    } else {
      return outputs;
    }
  }
};

var getParam = function getParam(paramMode, param, ints) {
  switch (paramMode) {
    case 0:
      return Number(ints[param]) || 0;

    case 1:
      return param;

    case 2:
      return Number(ints[relativeBase + param]) || 0;
  }
};

var getWriteParam = function getWriteParam(paramMode, param) {
  switch (paramMode) {
    case 0:
    case 1:
      return param;

    case 2:
      return relativeBase + param;
  }
};

var add = function add(ints, _, __, paramModes, index) {
  var start = Number(ints[++index]);
  var end = Number(ints[++index]);
  var rawLocation = Number(ints[++index]);
  var location = getWriteParam(paramModes[2], rawLocation);
  var param1 = getParam(paramModes[0], start, ints);
  var param2 = getParam(paramModes[1], end, ints);
  ints[location] = Number(param1) + Number(param2);
  return index + 1;
};

var multiply = function multiply(ints, _, __, paramModes, index) {
  var start = Number(ints[++index]);
  var end = Number(ints[++index]);
  var rawLocation = Number(ints[++index]);
  var location = getWriteParam(paramModes[2], rawLocation);
  var param1 = getParam(paramModes[0], start, ints);
  var param2 = getParam(paramModes[1], end, ints);
  ints[location] = Number(param1) * Number(param2);
  return index + 1;
};

var input = function input(ints, inputs, _, paramModes, index) {
  var saveIndex = Number(ints[++index]);
  var toSave = getWriteParam(paramModes[0], saveIndex);
  var input = inputs.shift();
  ints[toSave] = input;
  return index + 1;
};

var output = function output(ints, _, outputs, paramModes, index) {
  var readIndex = Number(ints[++index]);
  var toOutput = getParam(paramModes[0], readIndex, ints);
  outputs.push(toOutput);
  return index + 1;
};

var jumpTrue = function jumpTrue(ints, inputs, outputs, paramModes, index) {
  var checker = Number(ints[++index]);
  var shouldJump = getParam(paramModes[0], checker, ints) !== 0;

  if (shouldJump) {
    var newIndexIndex = Number(ints[++index]);
    var newIndex = getParam(paramModes[1], newIndexIndex, ints);
    return newIndex;
  }

  return index + 2;
};

var jumpFalse = function jumpFalse(ints, inputs, outputs, paramModes, index) {
  var checker = Number(ints[++index]);
  var shouldJump = getParam(paramModes[0], checker, ints) === 0;

  if (shouldJump) {
    var newIndexIndex = Number(ints[++index]);
    var newIndex = getParam(paramModes[1], newIndexIndex, ints);
    return newIndex;
  }

  return index + 2;
};

var lessThan = function lessThan(ints, inputs, outputs, paramModes, index) {
  var firstIndex = Number(ints[++index]);
  var secondIndex = Number(ints[++index]);
  var first = getParam(paramModes[0], firstIndex, ints);
  var second = getParam(paramModes[1], secondIndex, ints);
  var rawLocation = Number(ints[++index]);
  var location = getWriteParam(paramModes[2], rawLocation);
  var toStore = first < second ? 1 : 0;
  ints[location] = toStore;
  return index + 1;
};

var equals = function equals(ints, inputs, outputs, paramModes, index) {
  var firstIndex = Number(ints[++index]);
  var secondIndex = Number(ints[++index]);
  var first = getParam(paramModes[0], firstIndex, ints);
  var second = getParam(paramModes[1], secondIndex, ints);
  var rawLocation = Number(ints[++index]);
  var location = getWriteParam(paramModes[2], rawLocation);
  var toStore = first === second ? 1 : 0;
  ints[location] = toStore;
  return index + 1;
};

var setRelativeBase = function setRelativeBase(ints, inputs, outputs, paramModes, index) {
  var param = Number(ints[++index]);
  relativeBase += getParam(paramModes[0], param, ints);
  return index + 1;
};

var commands = {
  1: add,
  2: multiply,
  3: input,
  4: output,
  5: jumpTrue,
  6: jumpFalse,
  7: lessThan,
  8: equals,
  9: setRelativeBase
};

var getCommand = function getCommand(commandString) {
  var commandInt = commandString.slice(-2);
  var param1 = 0,
      param2 = 0,
      param3 = 0;

  if (commandString.length > 2) {
    param1 = Number(commandString.slice(-3, -2));
  }

  if (commandString.length > 3) {
    param2 = Number(commandString.slice(-4, -3));
  }

  if (commandString.length > 4) {
    param3 = Number(commandString.slice(-5, -4));
  }

  return {
    command: commands[Number(commandInt)],
    paramModes: [param1, param2, param3]
  };
};