module.exports = function (data) {
  var width = 25;
  var height = 6;
  var pixels = data.split("");
  var pixelsPerLayer = width * height;
  var numLayers = data.length / pixelsPerLayer;
  var image = "2".repeat(pixelsPerLayer).split("");

  for (var layerNum = 0; layerNum < numLayers; layerNum++) {
    var first = layerNum * pixelsPerLayer;
    var last = first + pixelsPerLayer - 1;
    var layer = pixels.slice(first, last + 1);
    layer.forEach(function (pixel, index) {
      if (image[index] == 2 && pixel != 2) {
        image[index] = pixel;
      }
    });
  }

  var lines = [];

  for (var i = 0; i < height; i++) {
    lines.push(image.slice(i * width, i * width + width));
  }

  console.log(lines);
  return lines.join("\n");
};